.employees_wrapper {
  display: flex;
  flex-direction: column;
  width: 50%;
  border-left: 1px solid black;
  border-right: 1px solid black;
}

.basic_container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.employees_container {
  width: 28%;
  padding: 3% 0 0 3%;
  border-bottom: 1px solid gray;
}

.employee_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2% 7%;
}

.head_style {
  font-weight: bold;
  font-size: 1.2rem;
  padding: 5% 0;
  background-color: #46b3e6;
}
